@import "../../assets/styles/globalStyles.scss";

.tabs {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .tabs-list {
    display: flex;
    align-items: center;
    justify-content: center;

    .tabs-list-header {
      width: 100%;
      padding: 20px;
      font-size: map-get($font-sizes, "md");
      font-weight: bold;
      text-align: center;
      margin-bottom: 15px;
      border-bottom: 2px solid map-get($colors, "secondary");
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;

      &.tabs-list-header_active {
        border-bottom: 4px solid map-get($colors, "primary");
      }
    }
  }

  .tabs-content {
    width: 100%;
    font-size: 1.25rem;
    color: map-get($colors, "secondary");
    display: none;
    &.tabs-content_active {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
  }
}